:root{
	--red: #c42b2c;
	--baige: #F5EED7;

	--background: var(--baige);
	--text: var(--red);
	--text-body: #000;

	--inverted-background: var(--red);
	--inverted-text: var(--baige);
	--inverted-text-body: var(--baige);
}

body{
	background: var(--background);
	text-align: left;

    font-family: 'Petrona', sans-serif;
	font-weight: normal;
	font-size: 19px;
	letter-spacing: 0.5px;
}
body.sheetIsOpen{
	overflow: hidden;
	pointer-events: none;
}

.svg-header{
	background: var(--inverted-background);
	fill: var(--inverted-text);
	width: 100%;
	max-height: 64vh;
	padding: 64px;
	padding: 8vh;
}

.intro_text{
	width: 100%;
	padding: 0 64px 64px 64px;
	margin: -10px 0 0 0;
	background: var(--inverted-background);
	color: var(--inverted-text);
	text-align: center;
}
.intro_text .inner{
	display: inline-block;
	width: auto;
	max-width: 100%;
	margin: 0 auto;
	text-align: left;
}


.footer{
	padding: 64px 64px 0 64px;
	text-align: center;
}
.footer_logos{
	padding: calc(32px - 8px) 32px calc(64px - 8px) 32px;
	text-align: center;
}
.footer_logos img{
	height: 64px;
	margin: 8px 32px;
}
.thomasLine{
	color: white;
	background: black;
	padding: 16px;
	text-align: center;
}
.thomasLine a,
.thomasLine a:visited{
	color: inherit;
	text-decoration: underline;
}
.thomasLine a:hover{
	opacity: 0.7;
}

a,
a:visited{
	color: var(--text);
	text-decoration: none;
}
a:hover{
	color: var(--text);
	text-decoration: underline;
}

h1,
.h1{
	font-family: 'Playfair Display', serif;
	font-weight: 400;
	font-size: 99px;
	letter-spacing: -1.5px;
}

h2,
.h2{
	font-family: 'Playfair Display', serif;
	font-weight: 400;
	font-size: 62px;
	letter-spacing: -0.5px;
}

h3,
.h3{
	font-family: 'Playfair Display', serif;
	font-weight: 400;
	font-size: 49px;
	letter-spacing: 0px;

	margin-top: 42px;
}

h4,
.h4{
	font-family: 'Playfair Display', serif;
	font-weight: 700;
	font-size: 35px;
	letter-spacing: 0.25px;
}

hr{
	border: 0;
	height: 1px;
	background: var(--text);
	margin: 40px 0;
}


